import React from 'react'
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import LogoData from './LogoData'
import ArtData from './ArtData'


class Gallery extends React.Component {

  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.focus = new URLSearchParams(window.location.search).get("focus");
    this.path = window.location.pathname;

    if(this.path == "/art-gallery") {
      this.show = ArtData.findIndex(l => l.name == this.focus);
    } else {
      this.show = LogoData.findIndex(l => l.name == this.focus);
    }

    // no query param
    if(this.show == -1) {
      this.show = 0;
    }
 this.state = {
    slideIndex: 0
  }

  }

  next() {
    this.slider.slickNext();
  }

  previous() {
    this.slider.slickPrev();
  }

  render() {
    var settings = {
      infinite: true,
      initialSlide: this.show,
      focusOnClick: true,
      arrows: false,
      adaptiveHeight: true,
      dots: true,
      beforeChange: (current, next) => this.setState({ slideIndex: next })
    };


    return (
      <div className="pure-g gallery-grid">
        <div className="pure-u-sm-1 pure-u-md-1-5 pure-u-lg-1-5"></div>
        <div className="pure-u-sm-1 pure-u-md-3-5 pure-u-lg-3-5">
          <div>
            <Slider ref={c => (this.slider = c)} {...settings}>
              {this.props.logodata.map(logo => <img key={logo.name} onClick={this.next} className="pure-img" alt={logo.name} src={require(`${logo.image}`)}></img>)}
            </Slider>
            <div class="centerme">
              <p>{this.props.logodata[this.state.slideIndex].name}</p>
            </div>
          </div>
          <div className="pure-u-sm-1 pure-u-md-1-5 pure-u-lg-1-5"></div>
        </div>
      </div>
    )
  }
}

export default Gallery
